<template>
  <main class="main">
    <Thumbnail v-for="art in displayedArt" :art="art" :key="art.id" />
    <ButtonContainer 
      :currentPage="currentPage"
      :fetchNextPage="fetchNextPage" />
  </main>
</template>

<script>
import fetchArt from '../api/fetchArt';
import Thumbnail from './Thumbnail.vue';
import ButtonContainer from './ButtonContainer.vue';

export default {
  name: 'Main',
  created() {
    this.fetchNextPage(1);
  },
  data() {
    return {
      displayedArt: [],
      currentPage: 0,
    };
  },
  methods: {
    async fetchNextPage(page) {
      const fetchedArt = await fetchArt(page);

      this.displayedArt = [...this.displayedArt, ...fetchedArt];
      this.currentPage = this.currentPage + 1;
    },
  },
  components: {
    Thumbnail,
    ButtonContainer,
  },
};
</script>

<style lang="scss">
.main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 2%;
}

@media screen and (max-width: 414px) {
  .main {
    flex-direction: column;
    justify-content: center;
  }
}
</style>
